<template>
    <div class="container1">
      <div>
        <div class="form_imput">
          <form action="#" method="POST">
            <input type="file" @change="loadTextFromFile" accept=".xml">
            <p>Nhp chut  chn ha n.</p>
          </form>
        </div>
        <div>
            <b>Lu :</b> Trng hp c vng mc v ni dung c ha n hoc hin th thng tin ha n, Anh/Ch vui lng gi thng tin qua Email: <b>trutc@smartsign.com.vn</b> hoc lin h qua s Hotline: <b>0909052248</b>  c h tr.
        </div>
        <div class="verify_info">
          <div>
            <p v-if="isValid"><i class="fa fa-check-circle text-green" aria-hidden="true"></i> <b>Ni dung ha n cn nguyn vn</b></p>
            <p v-if="isNotValid"><i class="fa fa-exclamation-triangle text-orange" aria-hidden="true"></i> <b>Ha n  b chnh sa</b></p>
            <p v-if="isNotPubicKey"><i class="fa fa-exclamation-triangle text-orange" aria-hidden="true"></i> <b>Khng tm thy ch k s trn ha n</b></p>
          </div>
          <div>
            <p v-html="KHMSHDon"></p>
          </div>
        </div>
        <div class="about_us">
          <h2>Gii thiu chng trnh c ha n in t xml</h2>
          <p>Hin nay trn th trng c rt nhiu nh cung cp ha n in t khc nhau, theo quy nh ca b thng tin truyn thng th ha n in t c lu di nh dng xml ch c gi tr khi c k bng ch k s ca doanh nghip pht hnh ha n
            . Ha n c lu di dng xml  m bo dung lng lu tr (chim t dung lng b nh) v tnh xc thc (C th k bng ch k s).</p>
          <p>Tuy nhin ha n in t xml cng gy kh khn v xem ha n v xc thc ha n trn file ha n xml. Chng trnh c ha n xml ca SmartSign s gip bn c th kim tra ch k s v kim tra ha n 1 cch d dng hn</p>
        </div>
      </div>
    </div>
</template>
<script>
import "regenerator-runtime/runtime";
let dom = require('xmldom').DOMParser;
let select = require('xml-crypto').xpath
let SignedXml = require('xml-crypto').SignedXml
export default {
  data () {
    return {
      isValid:false,
      isNotValid:false,
      isShowLang:false,
      isNotPubicKey:false,
      KHMSHDon:''
    }
  },
  methods: {
    loadTextFromFile(e) {
     let files = e.target.files || e.dataTransfer.files;
     if (!files.length) return;
     var temp = this.readFile(files[0]);
    },
    readFile(file) {
        let reader = new FileReader();
        reader.onload = e => {
            let json = e.target.result;
            let doc = new dom().parseFromString(json);
            var signature = select(doc, "//*[local-name(.)='Signature' and namespace-uri(.)='http://www.w3.org/2000/09/xmldsig#']")[0]
            var publickey = select(doc, "//*[local-name(.)='X509Certificate']")[0]
            var KHMSHDon = select(doc, "//*[local-name(.)='KHMSHDon']")[0];
            console.log(publickey);
            if(publickey!='undefined'&&publickey!=undefined){
              if(KHMSHDon!='undefined'){
                this.KHMSHDon = '<i class="fa fa-check-circle text-green" aria-hidden="true"></i> <b>Mu s: </b> ' +KHMSHDon.firstChild;
              }
              else{
                this.KHMSHDon = '';
              }
              var sig = new SignedXml()
              sig.keyInfoProvider = new this.MyKeyInfo(publickey.firstChild)
              sig.loadSignature(signature)
              
              var res = sig.checkSignature(json)
              if (!res)
              {
                this.isValid= false
                this.isNotValid = true;
                this.isNotPubicKey  = false;
                console.log(sig.validationErrors)
              }
              else{
                this.isValid= true
                this.isNotValid = false;
                this.isNotPubicKey  = false;
                
              }
            }
            else{
              this.isValid= false;
              this.isNotValid = false;
              this.isNotPubicKey  = true;
            }
        };
        reader.readAsText(file);
   },
   MyKeyInfo(signature) {
	  this.getKeyInfo = function(key, prefix) {
        prefix = prefix || ''
        prefix = prefix ? prefix + ':' : prefix
	    return "<" + prefix + "X509Data></" + prefix + "X509Data>"
	  }
	  this.getKey = function(keyInfo) {
	    //you can use the keyInfo parameter to extract the key in any way you want      
	    return '-----BEGIN CERTIFICATE-----'+signature+'-----END CERTIFICATE-----';
	  }
	}
  }
};
</script>


